/*  공통 
==================================== */
.visuallyhidden {
  position: absolute; 
  width: 1px;
  height: 1px;
  margin: -1px; 
  clip-path: polygon(0 0, 0 0, 0 0);
  clip-path: inset(50%); 
  clip: rect(0, 0, 0, 0); 
  clip: rect(0 0 0 0);
  overflow: hidden; }

.none_scroll {overflow: hidden; touch-action: none;}
.indent {text-indent: -9999px;}

.inner{ 
  &40 {
    position:relative;
    padding: 0 40px;
    box-sizing: border-box;
  }
  &20 {
    position: relative; 
    padding: 0 20px; 
    box-sizing: border-box;
  }
}



/* width */
.w {
  &.full {
    width: 100%;
  }
}


/* height */
.h{
  &-50 {
    height: 50px;
  }
  &-40 {
    height: 40px;
  }
  &-30 {
    height: 30px;
  }
  &-20 {
    height: 20px;
  }
}

/* border-radius */
.radius {
  border-radius: 100%;
  &-5 {
    border-radius: 5px;
  }
  &-20 {
    border-radius: 20px;
  }
}

/* font size */
.font {
  &-18 {
    font-size: 18px;
  }
  &-16 {
    font-size: 16px;
  }
}
